
let socketTask = null;
let reconnectTimer = null;
let reconnectCount = 0;
const MAX_RECONNECT = 6;

let currentUserId = null;
let messageQueue = [];
const QUEUE_KEY = 'socket_message_queue';

// Â≠òÊîæÊ∂àÊÅØÁä∂ÊÄÅÂõûË∞ÉÔºåkey‰∏∫msgIdÔºåvalue‰∏∫ÂõûË∞ÉÂáΩÊï∞
const msgStatusCallbacks = new Map();

// Â≠òÊîæÊ∂àÊÅØACKË∂ÖÊó∂ÂÆöÊó∂Âô®Ôºåkey‰∏∫msgIdÔºåvalue‰∏∫ÂÆöÊó∂Âô®ID
const ackTimers = new Map();

const CONNECT_STATUS = {
    DISCONNECTED: 0,
    CONNECTING: 1,
    CONNECTED: 2,
};
let connectStatus = CONNECT_STATUS.DISCONNECTED;

function generateUUID() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        const r = Math.random() * 16 | 0,
            v = c === 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}

function persistQueue() {
    try {
        uni.setStorageSync(QUEUE_KEY, messageQueue);
        console.log('[socket] ÁºìÂ≠òÈòüÂàóÂ∑≤ÊåÅ‰πÖÂåñÔºåÈïøÂ∫¶:', messageQueue.length);
    } catch (e) {
        console.error('[socket] persistQueue error', e);
    }
}

function loadQueueFromStorage() {
    try {
        const q = uni.getStorageSync(QUEUE_KEY);
        messageQueue = Array.isArray(q) ? q : [];
        console.log('[socket] ‰ªéÊú¨Âú∞ÁºìÂ≠òÊÅ¢Â§çÈòüÂàóÔºåÈïøÂ∫¶:', messageQueue.length);
    } catch (e) {
        messageQueue = [];
    }
}

export function connectSocket(userId, onMessage) {
    if (connectStatus === CONNECT_STATUS.CONNECTED || connectStatus === CONNECT_STATUS.CONNECTING) {
        console.warn('WebSocket Â∑≤ÁªèËøûÊé•ÊàñÊ≠£Âú®ËøûÊé•‰∏≠ÔºåË∑≥ËøáÈáçÂ§çËøûÊé•');
        return;
    }

    currentUserId = userId;
    connectStatus = CONNECT_STATUS.CONNECTING;
    console.log('[socket] ÂáÜÂ§áËøûÊé• WebSocketÔºåÁî®Êà∑ID:', userId);

    const wsUrl = `ws://192.168.2.5:9326?name=${encodeURIComponent(userId)}`; // ËØ∑Ê†πÊçÆÂÆûÈôÖIPÁ´ØÂè£ÊõøÊç¢
    socketTask = uni.connectSocket({
        url: wsUrl,
        success() {
            console.log('WebSocket ËøûÊé•ËØ∑Ë°åÊ±ÇÂèëËµ∑');
        },
        fail(err) {
            console.error('WebSocket ËøûÊé•ËØ∑Ê±ÇÂ§±Ë¥•', err);
            attemptReconnect(onMessage);
        },
    });

    socketTask.onOpen(() => {
        console.log('üì° WebSocket Â∑≤ÊâìÂºÄ');
        connectStatus = CONNECT_STATUS.CONNECTED;
        reconnectCount = 0;

        // ÂèëÈÄÅÁôªÂΩïÊ∂àÊÅØ
        const loginData = {
            cmd: 1,
            from: currentUserId,
        };
        socketTask.send({ data: JSON.stringify(loginData) });

        loadQueueFromStorage();
        flushQueue();
    });

    socketTask.onMessage((res) => {
        const dataStr = res.data;
        if (!dataStr || dataStr === 'null' || dataStr === 'undefined') {
            console.warn('Êî∂Âà∞Êó†ÊïàÊ∂àÊÅØ:', dataStr);
            return;
        }
        if (dataStr.trim().startsWith('{') || dataStr.trim().startsWith('[')) {
            try {
                const data = JSON.parse(dataStr);

                // Â§ÑÁêÜACKÊ∂àÊÅØÔºåcmd == -1 Ë°®Á§∫ÂêéÁ´ØÁ°ÆËÆ§Êî∂Âà∞Ê∂àÊÅØ
                if (data.cmd === -1 && data.msgId) {
                    const cb = msgStatusCallbacks.get(data.msgId);
                    if (cb) {
                        cb('success'); // Ê†áËÆ∞ÊàêÂäü
                        console.log('ÂÆâÂÖ®Êè°ÊâãÊàêÂäüÔΩû')
                        msgStatusCallbacks.delete(data.msgId);
                    }
                    // Ê∏ÖÁêÜÂØπÂ∫îACKË∂ÖÊó∂ÂÆöÊó∂Âô®
                    if (ackTimers.has(data.msgId)) {
                        clearTimeout(ackTimers.get(data.msgId));
                        ackTimers.delete(data.msgId);
                    }
                    return; // ‰∏çËΩ¨ÂèëACKÊ∂àÊÅØÁªô‰∏öÂä°Â§ÑÁêÜÔºåÈò≤Ê≠¢ÈáçÂ§çÊòæÁ§∫
                }

                // ÈùûACKÊôÆÈÄöÊ∂àÊÅØÂõûË∞É
                onMessage && onMessage(data);
            } catch (e) {
                console.error('Ê∂àÊÅØËß£ÊûêÈîôËØØ', e, dataStr);
            }
        } else {
            console.log('Êî∂Âà∞Èùû JSON Ê∂àÊÅØ:', dataStr);
        }
    });

    socketTask.onClose(() => {
        console.log('WebSocket Â∑≤ÂÖ≥Èó≠');
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        attemptReconnect(onMessage);
    });

    socketTask.onError((err) => {
        console.error('WebSocket ÈîôËØØ', err);
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        attemptReconnect(onMessage);
    });
}

function attemptReconnect(onMessage) {
    if (reconnectCount >= MAX_RECONNECT) {
        console.warn('ÈáçËøûÊ¨°Êï∞ËææÂà∞‰∏äÈôêÔºåÂÅúÊ≠¢ÈáçËøû');
        return;
    }
    if (reconnectTimer) return;

    reconnectCount++;
    const delay = Math.min(30000, 5000 * Math.pow(2, reconnectCount - 1));
    console.log(`Á¨¨${reconnectCount}Ê¨°ÈáçËøûÔºå${delay}msÂêéÂ∞ùËØï`);

    reconnectTimer = setTimeout(() => {
        reconnectTimer = null;
        connectSocket(currentUserId, onMessage);
    }, delay);
}

function flushQueue() {
    if (!messageQueue.length) {
        console.log('[socket] flushQueueÔºöÊó†ÁºìÂ≠òÊ∂àÊÅØÈúÄË¶ÅÂèëÈÄÅ');
        return;
    }
    console.log('[socket] flushQueue start, Ê∂àÊÅØÊï∞Èáè:', messageQueue.length);

    const sendNext = () => {
        if (!messageQueue.length) {
            console.log('[socket] flushQueue ÂÆåÊàêÔºåÁºìÂ≠òÈòüÂàóÊ∏ÖÁ©∫');
            persistQueue();
            return;
        }
        const item = messageQueue[0];
        try {
            socketTask.send({
                data: JSON.stringify(item),
                success() {
                    console.log('[socket] flushQueue ÂèëÈÄÅÊàêÂäü:', item);
                    if (item.msgId && msgStatusCallbacks.has(item.msgId)) {
                        msgStatusCallbacks.get(item.msgId)('sending');
                        // ËÆæÁΩÆACKË∂ÖÊó∂ÂÆöÊó∂Âô®
                        if (ackTimers.has(item.msgId)) {
                            clearTimeout(ackTimers.get(item.msgId));
                        }
                        const timerId = setTimeout(() => {
                            const cb = msgStatusCallbacks.get(item.msgId);
                            if (cb) {
                                cb('failed'); // Ë∂ÖÊó∂Êú™Êî∂Âà∞ACKÊ†áËÆ∞Â§±Ë¥•
                                msgStatusCallbacks.delete(item.msgId);
                            }
                            ackTimers.delete(item.msgId);
                        }, 5000);
                        ackTimers.set(item.msgId, timerId);
                    }
                    messageQueue.shift();
                    persistQueue();
                    setTimeout(sendNext, 50);
                },
                fail(err) {
                    console.warn('[socket] flush fail', err);
                    if (item.msgId && msgStatusCallbacks.has(item.msgId)) {
                        msgStatusCallbacks.get(item.msgId)('failed');
                    }
                },
            });
        } catch (e) {
            console.error('[socket] flush exception', e);
        }
    };
    sendNext();
}

function sendData(data, onStatusChange) {
    if (!data.msgId) {
        data.msgId = generateUUID();
    }
    if (onStatusChange && typeof onStatusChange === 'function') {
        msgStatusCallbacks.set(data.msgId, onStatusChange);
    }
    if (connectStatus !== CONNECT_STATUS.CONNECTED || !socketTask) {
        console.warn('WebSocket Êú™ËøûÊé•ÔºåÊ∂àÊÅØÂä†ÂÖ•ÈòüÂàóÁºìÂ≠ò', data);
        messageQueue.push(data);
        persistQueue();
        if (onStatusChange) onStatusChange('failed');
        return;
    }
    try {
        socketTask.send({
            data: JSON.stringify(data),
            success() {
                console.log('[socket] Ê∂àÊÅØÂèëÈÄÅÊàêÂäü', data);
                if (onStatusChange) onStatusChange('sending');
                // ËÆæÁΩÆACKË∂ÖÊó∂ÂÆöÊó∂Âô®
                if (ackTimers.has(data.msgId)) {
                    clearTimeout(ackTimers.get(data.msgId));
                }
                const timerId = setTimeout(() => {
                    const cb = msgStatusCallbacks.get(data.msgId);
                    if (cb) {
                        cb('failed'); // Ë∂ÖÊó∂Êú™Êî∂Âà∞ACKÊ†áËÆ∞Â§±Ë¥•
                        msgStatusCallbacks.delete(data.msgId);
                    }
                    ackTimers.delete(data.msgId);
                }, 5000);
                ackTimers.set(data.msgId, timerId);
            },
            fail(err) {
                console.error('ÂèëÈÄÅÊ∂àÊÅØÂ§±Ë¥•ÔºåÂä†ÂÖ•ÁºìÂ≠ò', err, data);
                messageQueue.push(data);
                persistQueue();
                if (onStatusChange) onStatusChange('failed');
            },
        });
    } catch (e) {
        console.error('ÂèëÈÄÅÊ∂àÊÅØÂºÇÂ∏∏ÔºåÊ∂àÊÅØÂä†ÂÖ•ÁºìÂ≠ò', e, data);
        messageQueue.push(data);
        persistQueue();
        if (onStatusChange) onStatusChange('failed');
    }
}

export function sendMsg(toUserId, msg, fromUserId, onStatusChange) {
    const data = {
        cmd: 2,
        type: 'private',
        from: fromUserId,
        to: toUserId,
        message: msg,
        timestamp: Date.now(),
    };
    sendData(data, onStatusChange);
}

export function sendGroupMsg(groupId, msg, fromUserId, onStatusChange) {
    const data = {
        cmd: 3,
        type: 'group',
        from: fromUserId,
        to: groupId,
        message: msg,
        timestamp: Date.now(),
    };
    sendData(data, onStatusChange);
}

export function retrySend(msgObj, onStatusChange) {
    if (!msgObj.msgId) {
        console.warn('retrySend Áº∫Â∞ë msgIdÔºåÊó†Ê≥ïÈáçÂèë');
        return;
    }
    console.log('[socket] retrySend ÈáçÂèëÊ∂àÊÅØ', msgObj);
    sendData(msgObj, onStatusChange);
}

export function closeSocket() {
    if (socketTask) {
        console.log('[socket] ‰∏ªÂä®ÂÖ≥Èó≠ WebSocket ËøûÊé•');
        socketTask.close();
        socketTask = null;
        connectStatus = CONNECT_STATUS.DISCONNECTED;
        if (reconnectTimer) {
            clearTimeout(reconnectTimer);
            reconnectTimer = null;
        }
        reconnectCount = 0;
    }
}

export function isConnected() {
    return connectStatus === CONNECT_STATUS.CONNECTED;
}